            val scaffoldState = rememberScaffoldState()
            val scope = rememberCoroutineScope()
            Scaffold (
                scaffoldState = scaffoldState,
                topBar = {
                    TopAppBar {
                        IconButton(
                            onClick = {
                                scope.launch { scaffoldState.drawerState.open() }
                            }
                        ) {
                            Icon(Icons.Filled.Menu,"")
                        }
                        Text("Welcome to William's KMP web-site!")
                    }
                },
                drawerContent = {
                    NavigationDrawer(
                        name = "William Bell",
                        email = "wbell09@gmail.com",
                        items = DrawerItem.entries
                    ) {
                        when (it) {
                            DrawerItem.ABOUT -> {
                                println()
                            }
                            DrawerItem.SETTINGS -> {
                                println()
                            }
                            DrawerItem.RECENT -> {
                                println()
                            }
                            DrawerItem.UPLOADS -> {
                                println()
                            }
                        }
                        scope.launch {
                            scaffoldState.drawerState.close()
                            println("Close Drawer")
                        }
                    }
                },
                bottomBar = {
                    BottomNavigation {
                        TabNavigationItem(HomeTab)
                        TabNavigationItem(VideosTab)
                        TabNavigationItem(GetAppTab)
                        TabNavigationItem(ProfileTab)
                        TabNavigationItem(SettingsTab)
                    }
                }
            ) {
                Column(
                    modifier = Modifier.padding(it).fillMaxSize()
                ){
                CurrentTab()
                    }
            }
        }
        //Navigator(HomeScreen()){navigator-> SlideTransition(navigator)  }
    }
}

@Composable
private fun RowScope.TabNavigationItem(tab: Tab){
    val tabNavigator = cafe.adriel.voyager.navigator.tab.LocalTabNavigator.current
    BottomNavigationItem(
        selected = tabNavigator.current == tab,
        onClick = { tabNavigator.current = tab},
        label = { Text(tab.options.title) },
        icon = {}
    )
}